.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    background-color: rgba(111, 111, 111, 0.5);
    /* 배경 색상을 흰색에 50% 투명도로 설정합니다. */
    z-index: 10;
    /* 다른 요소들보다 더 높은 값을 지정해 줍니다. */
    transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 10px;
    width: 80%;
    max-width: 500px;
}


