.name_fwrap input{
    

}

.modal-content button {
    margin-bottom: 10px;
    margin-right: 3px;
    /* 버튼 요소들 간의 간격을 3px로 설정합니다. */
    color: black;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 5px;
    font-weight: 600;
    transition: background-color 0.3s ease, color 0.3s ease;
    /* hover 효과의 부드러운 전환을 위해 transition 속성을 추가합니다. */
}

.modal-content button:hover {
    background-color: #421f69;
    /* 마우스를 올렸을 때의 배경색을 purple로 설정합니다. */
    color: white;
    /* 마우스를 올렸을 때의 글자색을 white로 설정합니다. */
}

.modal.open {
    opacity: 1;
    visibility: visible;
}